<template>

    <div class="BGC" >
        <div class="container">
            <div class="row">
                <div class="step">
                    <el-steps :active="1"   finish-status="success" simple  style="margin-top: 15px">
                    <el-step title="" ></el-step>
                    <el-step title="" ></el-step>
                    <el-step title="" ></el-step>
                    </el-steps>
                </div>
                <el-divider></el-divider>
                <h1></h1>
                <el-divider></el-divider>
                <div class="row ">  
                            <p class="col-3 me-4"></p>
                            <p class="col-4 "></p>
                            <p class="col-2 "></p>
                            <p class="col-2 "></p>
                </div>
                <div>
                
                    <ul  v-for="i in $store.state.ItemList" class="col-12" :key="i.name" >

                        <el-row class="rowList" >
                            <el-divider></el-divider>
                            <div class="message row">
                                <input type="checkbox" class="checkInput col-1" v-model="i.done" @click="checkItem(i.name)" name="" id="">
                                <img class="col-2" :src="i.url">
                                <p class="col-4">{{i.name}}</p>
                                <p class="col-2">$ {{i.price}}</p> 
                                <p class="col-2">$ {{i.itemCount*i.price}}</p>
                            </div>
                            <el-input-number class="col" v-model="i.itemCount"  :min="1" :max="10"  value="1" label=""></el-input-number>
                            <el-button class="DeleteBtn"  @click="handleDelete(i.name)" type="danger" icon="el-icon-delete" circle></el-button> 
                       </el-row>
                    </ul>
                </div>
            </div>
                        <p class="paddingbottom"></p>
                        <el-divider></el-divider>
            <div class="row footerContent">
                        <input class="col-2" v-model="isAll" type="checkbox"  name="" id="">
                        <p class="col-5" > :   {{toval}} </p>
                        <p class="col-5">{{doneTotal}}{{total}}</p>
                        <el-button class="col-5 bt" type="success" @click="gotoCheck"></el-button>
                        <el-button class="col-5 bt" @click="clearAllTodo" type="danger"></el-button>              
            </div>
                        <div v-show="!total" style="min-height: 530px;"> </div>
                        <el-divider></el-divider>
                        <p class="paddingbottom"></p> 
        </div>
    </div>
    
</template>

<script>
export default {
    name:'ShoppingCart',
    data(){
        return{
                toval : 0,
                // 'this.$store.state.ItemList':JSON.parse(localStorage.getItem('todos'))||[]
        }

    },
    
    mounted(){
        //
            let sum = 0
            this.$store.state.ItemList.forEach(i=>{ sum+=(i.price*i.itemCount) })
            this.toval = sum
            this.$store.commit('TOTALVALUE',{sum})
        //,localStorage,,
        if(localStorage.getItem('todos') != null){
            //
            this.$store.state.ItemList = JSON.parse(localStorage.getItem('todos'))
        }
        
    },
    computed:{
        total(){
            return this.$store.state.ItemList.length
        },
        doneTotal(){
                //
                return this.$store.state.ItemList.reduce( (pre,current)=> pre+(current.done ? 1 : 0) ,0 )
        },
        isAll:{
            get(){
            return this.doneTotal === this.total
            },
            set(value){
                this.$store.commit('CHECKALLTODO',value)
            }
        },
    }
    ,
    methods:{
        //
        checkItem(name){
            this.$store.commit('CHEACKBOX',name)
        },
        //
        handleDelete(name){
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: '!',                     
                        })
                        this.$store.state.ItemList.some(item=>{
                            if(item.name == name ){
                                //
                                let ItemIndex = this.$store.state.ItemList.indexOf(item)
                                //indexmutations
                                this.$store.commit('DELETEITEM',ItemIndex)
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });          
                    });
        },
        //
        clearAllTodo(){
                //
                let minCheck =this.$store.state.ItemList.some(i=>{
                    return i.done == true
                    
                })
                //
                if(minCheck){
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '!'
                        
                    })
                    this.$store.commit('CLEARALLTODO')
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                    });
                //
                }else{
                    this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {
                        this.$message({
                        type: 'info',
                        message: ``
                            })
                        }
                    })
                }
        },
        //
        gotoCheck(){
            this.$router.push({path:'/checkout'})
            
        }


    },
    //
    updated(){
         
            let sum = 0
            this.$store.state.ItemList.forEach(i=>{
                sum+=(i.price*i.itemCount)
            })
            this.toval = sum     
            this.$store.commit('TOTALVALUE',{sum})
    },
    watch:{
       '$store.state.ItemList':{
           deep:true,
           handler(value){
                //state
                if(this.$store.state.ItemList.length == 0){
                    localStorage.removeItem('todos')
                }else{
                    //localStorage
                    localStorage.setItem('todos',JSON.stringify(value))
                }
           }
       }
    }
    
}
</script>

<style scoped>
    .BGC{
        padding-top: 50px;
        background-color: #f7f7f7;
        min-height: 830px;
    }

    .BGC H1{
        /* display: none; */
        /* margin: auto; */
        text-align: center;
    }

    .paddingbottom{
        padding-bottom: 50px;
    }

    .DeleteBtn{
           display: none;
    }
    .rowList{
        position: relative;
    }
    .rowList:hover{
        background-color: #ddd;
    }

    .rowList:hover .DeleteBtn{
           display: block ;
           position: absolute;
           right: 0;
           top: 0;
    }
    .footerContent{
        justify-content: center;
    }
    img{
        height: 135px;
        width: 135px;
    }
    

    
  @media screen and (max-width:767px) {
    .el-message-box{
        width: 300px;
        background-color: #f00;
    }



    .rowList{
        width: 100%;
    }
    .rowList p{
        display: flex;
        flex-wrap: wrap;
        font-size: 1rem;
    
    }
    .rowList .el-input-number{
        transform: scale(0.8);
    }

    .rowList img{
         height: 80px;
         width: 80px;
      }

    .footerContent{
        display: flex;
        flex-wrap: wrap;
    }
    .footerContent p{
        width: 155px;
        font-size: 13px;
    }
    .footerContent .bt{
        margin: 6px 50px;
        width: 200px;
    }


  }

</style>

<style>
    @media screen and (max-width:767px) {
        .el-message-box{
            width: 80%;
        }
    }
</style>